@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
html {
  font-size: 16px; }
  @media screen and (min-width: 320px) {
    html {
      font-size: calc(16px + strip-unit(8px) * ((100vw - 320px) / strip-unit(880px))); } }
  @media screen and (min-width: 1200px) {
    html {
      font-size: 24px; } }

html, body {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; }

