body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif, Georgia, 'Times New Roman', Times, serif, "Mali";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.smaller {
  height: 25px; }

.navbar {
  background-color: rgba(0, 0, 0, 0.7); }

.navbar-brand {
  color: #fff !important;
  font: 'Mali' !important;
  font-weight: 700; }

.nav-link {
  color: #fff !important;
  padding: .25rem .5rem; }
  .nav-link:hover {
    text-decoration: underline;
    color: #4ABDAC !important; }

.fixed-top {
  -webkit-transition: padding 0.2s ease;
  transition: padding 0.2s ease; }

.affix {
  padding-top: 1.2em !important;
  padding-bottom: 1.2em !important;
  -webkit-transition: padding 0.2s linear;
  transition: padding 0.2s linear; }

.custom-toggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(175,132,71, 0.7)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E"); }

.header {
  overflow: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))), url(/monicamarroyo/Monica_Salon_React/static/media/brick2.f7fe712c.jpg);
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/monicamarroyo/Monica_Salon_React/static/media/brick2.f7fe712c.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/monicamarroyo/Monica_Salon_React/static/media/brick2.f7fe712c.jpg);
  position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%; }

.container-fluid {
  display: flex;
  align-items: center;
  justify-content: center; }

img {
  border-style: solid; }

.header_img_container {
  margin-bottom: 60px;
  margin-top: 120px;
  margin-left: 40px;
  margin-right: 40px; }
  @media (max-width: 767px) {
    .header_img_container {
      margin-top: 20px; } }

.header_text_container {
  padding-top: 5%;
  padding-bottom: 0%; }
  @media (max-width: 767px) {
    .header_text_container {
      padding-bottom: 20px;
      padding-top: 60px;
      text-align: center; } }

.header-text-1 {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.25rem; }
  @media (max-width: 480px) {
    .header-text-1 {
      font-size: .7rem; } }

.header-text-2 {
  color: #fff;
  text-transform: uppercase; }
  @media (max-width: 480px) {
    .header-text-2 {
      font-size: 1.25rem; } }

@media only screen and (max-width: 600px) {
  .header_btn {
    margin-left: auto;
    margin-right: auto; } }

.container-fluid {
  display: flex;
  align-items: center;
  justify-content: center; }

.grid-services {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 30px;
  align-items: stretch;
  padding: 0;
  padding-bottom: 60px;
  border-bottom: 25px solid #00c5b1; }

.img-container {
  background-color: #00c5b1;
  width: 98px;
  height: 98px;
  padding: 25px;
  display: block;
  border-radius: 500px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto; }

.section-services:before, .section-services:after {
  position: relative;
  content: "";
  width: 30%;
  left: 35%;
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
  border-bottom: 5px dotted #4ABDAC; }

.grid-stylist {
  background-color: #252525;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  padding: 10px;
  grid-gap: 50px; }

.card {
  background-color: transparent !important;
  border-radius: none;
  border: none; }

.bg-img {
  background-size: cover;
  min-height: 180px;
  background-position: center;
  background-color: #ccc; }

.card-img_1 {
  min-height: 180px;
  background: url(/monicamarroyo/Monica_Salon_React/static/media/stylist1.52866f1e.jpg) no-repeat center;
  background-size: contain; }

.card-img_2 {
  min-height: 180px;
  background: url(/monicamarroyo/Monica_Salon_React/static/media/stylist2.7631af40.jpg) no-repeat center;
  background-size: contain; }

.card-img_3 {
  min-height: 180px;
  background: url(/monicamarroyo/Monica_Salon_React/static/media/stylist4.b566eb5c.jpg) no-repeat center;
  background-size: contain; }

.card-img_4 {
  min-height: 180px;
  background: url(/monicamarroyo/Monica_Salon_React/static/media/stylist5.1b5b2595.jpg) no-repeat center;
  background-size: contain; }

.card-img_5 {
  min-height: 180px;
  background: url(/monicamarroyo/Monica_Salon_React/static/media/stylist6.3d4b888e.jpg) no-repeat center;
  background-size: contain; }

.section-services:before, .section-services:after {
  position: relative;
  content: "";
  width: 30%;
  left: 35%;
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
  border-bottom: 5px dotted #4ABDAC; }

.section_location {
  position: relative;
  height: 300px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4edfca), to(#4ABDAC));
  background-image: -webkit-linear-gradient(#4edfca, #4ABDAC);
  background-image: linear-gradient(#4edfca, #4ABDAC); }

.section_location--text {
  padding: 45px 0;
  font: 44px;
  text-align: center; }

.section_location .section_location--text {
  color: white; }

svg {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px; }

@media (max-width: 699px) {
  .svg--lg {
    display: none; } }

@media (min-width: 700px) {
  .svg--sm {
    display: none; } }

.location-text-1:before, .location-text-1:after {
  position: relative;
  content: "";
  width: 15%;
  left: 43%;
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
  border-bottom: 5px dotted #4ABDAC; }

.loc_address {
  color: #484848; }
  @media (max-width: 575px) {
    .loc_address {
      text-align: center;
      font-size: 1.25rem; } }

@media (max-width: 575px) {
  .loc_address-text-2 {
    text-align: center;
    font-size: 2rem; } }

@media (max-width: 575px) {
  .loc_table {
    padding: 50px; } }

@media (max-width: 575px) {
  .loc_table_heading {
    text-align: center;
    font-size: 2rem; } }

